/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';

import {Spinner, DataTable, DataTableColumn, DataTableCell, Modal} from '@salesforce/design-system-react';
import SLDSPaginator from './SLDSPaginator';
import GRAXCSVExportButton from './GRAXCSVExportButton';

import {$, baseUrl, GetParams} from '../lib/grax-data-lake';
import {moment, SF_DATETIME_REGEXP, DATETIME_FORMAT, DATE_FORMAT, TZ} from '../lib/utils';

import _ from 'lodash';

const ROWSXPAGE = 20;


const GRAXTimeMachineCell = ({ children, ...props }) => {
	return (
        <DataTableCell title={children}>
            {children && (
                <a onClick={(e) => {
                    if(props.onClickHandler) {
                        props.onClickHandler(children);
                    }

                    e.preventDefault();
                }}>
                    Load this version
                </a>
            )}
        </DataTableCell>
    );
};
GRAXTimeMachineCell.displayName = DataTableCell.displayName;


export default class GRAXAuditDataTable extends React.Component {
	constructor(props) {
        super(props);

        this.state = {
			sortColumn: 'CreatedDate',
			sortDirection: 'desc',
            items: [],
            extraInfo: {},
            page: 0,
            isLoaded: false,
            isLoading: false,
            isTimeMachineModalOpen: false,
            timeMachineRecord: null
		};
    }

    shouldComponentUpdate(nextProps, nextState) {
        return this.props.renderKey !== nextProps.renderKey || this.state.items !== nextState.items || this.state.isLoaded !== nextState.isLoaded || this.state.isLoading !== nextState.isLoading || this.state.sortColumn !== nextState.sortColumn || this.state.sortDirection !== nextState.sortDirection || this.state.isTimeMachineModalOpen !== nextState.isTimeMachineModalOpen;
    }

    getDataHandler(page, sortColumn, sortDirection) {
        this.setState({
            isLoading: true,
            page: page,
            sortColumn: sortColumn,
            sortDirection: sortDirection
        });

        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake' + GetParams({
                action:'load',
                dataIndex: this.props.dataIndex,
                _select: _.union(this.props.columns, this.props.hiddenColumns).join(',') || 'Id',
                _q: this.props.filterQuery,
                _limit: ROWSXPAGE,
                _from: ROWSXPAGE * page,
                _sort: sortColumn + ' ' + sortDirection,
                _sortType: this.getSortFieldType(sortColumn)
            }),
            /*data: {
                _select: this.props.columns.join(',')
            },*/
            success: function(data) {
                var results = data.results;

                if(this.props.filterFunction) {
                    results = this.props.filterFunction(results);
                }

                results = _.map(results, (item, i) => {
                    _.forEach(item, (val, key) => {
                        if(typeof(val) == 'string') {
                            if(SF_DATETIME_REGEXP.test(val)) {
                                var m = moment(val);

                                if(m.isValid()) {
                                    item[key] = m.tz(TZ).format(m._i.indexOf(':') !== -1 ? DATETIME_FORMAT : DATE_FORMAT);
                                }
                            }
                        }
                    });

                    return {
                        ...item,
                        ...{id: i.toString()}
                    }
                });

                this.setState({
                    page: 0,
                    data: results,
                    extraInfo: data.extraInfo || {total: 0, size: ROWSXPAGE, totalRetrieved: 0},
                    items: results,
                    isLoaded: true,
                    isLoading: false
                });
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    isLoaded: true,
                    isLoading: false,
                    items: [],
                    error: err.toString()
                });
            }.bind(this)
        });
    }
    
    componentDidMount() {
        this.getDataHandler(0, this.state.sortColumn, this.state.sortDirection);
    }

	handleSort(sortColumn, ...rest) {        
        if(sortColumn.property) {
            this.getDataHandler(this.state.page, sortColumn.property, sortColumn.sortDirection);
        }
    }
    
    getSortFieldType(sortField) {
        if(!this.props._graxFieldsSelection) return null;

        return (this.props._graxFieldsSelection.find((item) => {
            return item.id === sortField;
        }) || {}).type;
    }

	render() {
		return this.state.isLoaded ? (
			<div>
                {this.state.isTimeMachineModalOpen ? (
                    <Modal
                        assistiveText={{ dialogLabel: 'Time Machine Record' }}
                        isOpen={this.state.isTimeMachineModalOpen}
                        onRequestClose={() => {
                            this.setState({
                                isTimeMachineModalOpen: false,
                                timeMachineRecord: null
                            });
                        }}
                        align="top"
                        size="large"
                    >
                        <section className="slds-p-around_medium" style={{
                            overflow: 'auto'
                        }}>
                            {JSON.stringify(this.state.timeMachineRecord)}
                        </section>
                    </Modal>
                ) : null}

                <div style={{position: 'relative'}}>
                    {this.state.isLoading && <Spinner size="large" variant="brand" />}
                    <DataTable
                        assistiveText={{
                            actionsHeader: 'actions',
                            columnSort: 'sort this column',
                            columnSortedAscending: 'asc',
                            columnSortedDescending: 'desc',
                            selectAllRows: 'all rows',
                            selectRow: 'select this row',
                        }}
                        fixedLayout
                        items={this.state.items}
                        id="GRAXAuditDataTable"
                        onSort={this.handleSort.bind(this)}
                    >

                        {this.props.columns.map((columnName) => (
                            <DataTableColumn
                                key={'col' + columnName}
                                id={columnName}
                                isSorted={this.state.sortColumn === columnName}
                                label={columnName.endsWith('.keyword') ? columnName.match(/(.+)\.keyword/)[1] : columnName}
                                property={columnName}
                                sortable
                                sortDirection={this.state.sortDirection}
                                isSortable={this.props.sortableColumns == null || this.props.sortableColumns.indexOf(columnName) !== -1}
                            >
                                {(columnName === 'ObjectVersion') ? (
                                    <GRAXTimeMachineCell onClickHandler={(timeMachineRecord) => {
                                        console.log({
                                            Key: timeMachineRecord.split(/(\/grax\/)/).slice(1).join('')
                                        });

                                        /*var params = {
                                            "orgid" : "00D1I000003nRMaUAM",
                                            "objecttype" : "Account",
                                            "objectid" : "0014600000VpLa3AAF",
                                            "filename" : "Account-0014600000VpLa3AAF-2018-10-23T15-38-14.json"
                                        };*/
                                        
                                        if(timeMachineRecord) {
                                            this.setState({
                                                isTimeMachineModalOpen: true,
                                                timeMachineRecord: timeMachineRecord
                                            });
                                        }
                                    }} />
                                ) : null}
                            </DataTableColumn>
                        ))}
                    </DataTable>
                </div>

                <div className="-x-small-buttons--horizontal slds-p-top_small">
                    <SLDSPaginator totalItems={this.state.items.length > 0 ? this.state.extraInfo.total: 0} pageSize={ROWSXPAGE} onChangePage={(page, isInitialLoad) => {
                        if(!isInitialLoad) {
                            this.getDataHandler(page - 1, this.state.sortColumn, this.state.sortDirection);
                        }
                    }} />
                </div>

                <div className="-x-small-buttons--horizontal slds-p-top_medium">
                    <GRAXCSVExportButton params={{
                        source: 'elastic',
                        dataIndex: this.props.dataIndex,
                        _select: _.union(this.props.columns, this.props.hiddenColumns).join(','),
                        _q: this.props.filterQuery,
                        _sort: this.state.sortColumn + ' ' + this.state.sortDirection,
                        _sortType: this.getSortFieldType(this.state.sortColumn),
                        columns: this.props.columns
                    }} />
                </div>
			</div>
		) : (
            <div style={{ position: 'relative', height: '2rem' }}>
                <Spinner size="medium" variant="brand" />
            </div>
        );
	}
}