import jQuery from 'jquery'

(function($) {
	var svgns = 'http://www.w3.org/2000/svg';
	var xlinkns = 'http://www.w3.org/1999/xlink';

    $.fn.sldsTree = function(options) {
    	var $this = this;

    	var settings = $.extend(true, {
        	data: [],
        	items: {
        		id: 'id',
        		label: 'label',
        		childs: 'childs',
        	},
        	attrs: {
				root: {'class': 'slds-tree_container'},
				ul: {'class': 'slds-tree'},
				li: {
					'role': 'treeitem',
					'aria-level': '1',
					'aria-expanded': 'false'
				},
				div: {'class': 'slds-tree__item'},
				button: {
					'class': 'slds-button slds-button_icon slds-m-right_x-small',
					'aria-hidden': 'true',
					'tabindex': '-1'
				}
			},
			expandButton: {
				'disabled': 'slds-is-disabled'
			},
			expandIcon: {
				'class': 'slds-button__icon slds-button__icon_small',
				'path': './assets/icons/utility-sprite/svg/symbols.svg#chevronright'
			},
        	onUl: function($ul, items, settings) {},
        	onLi: function($li, item, settings) {},
        	onDiv: function($div, item, settings) {
        		$div.append(item[settings.items.label]);
        	},
        	onButton: function($button, item, settings) {},
            onFinish: function($root, items, settings) {}
        }, options);

        var getTreeItemButton = function(item, disabled, $li) {
        	var $button = $('<button>').attr(settings.attrs.button).toggleClass(settings.expandButton.disabled, disabled);

        	var icon = document.createElementNS(svgns, 'svg');
        	icon.setAttribute('class', settings.expandIcon.class);
        	icon.setAttribute('aria-hidden', 'true');

        	var icon_use = document.createElementNS(svgns, 'use');
        	icon_use.setAttributeNS(xlinkns, 'xlink:href', settings.expandIcon.path);
        	
        	icon.appendChild(icon_use);

    		$button.append(icon);

        	$button.on('click', function(e) {
        		$li.attr('aria-expanded', function(i, attr) {
		            return !(attr === 'true');
		        });

		        e.preventDefault();
        	});

        	settings.onButton($button, item, settings);

        	return $button;
        }

        var getTreeItemContent = function(item, level, $ul, $li) {
        	var $div = $('<div>').attr(settings.attrs.div);

			$div.append(getTreeItemButton(item, !item[settings.items.childs] || item[settings.items.childs].length < 1, $li));

        	settings.onDiv($div, item, settings);

        	return $div;
        }

        var getTreeItem = function(item, level, $ul) {
        	var $li = $('<li>').attr(settings.attrs.li);

        	$li.attr({'aria-level': level}).append(getTreeItemContent(item, level, $ul, $li));
        	if(item[settings.items.childs]) {
        		$li.append(getTree(item[settings.items.childs], level+1));
        	}

        	settings.onLi($li, item, settings);

        	return $li;
        };

        var getTree = function(items, level) {
        	var $ul = $('<ul>').attr(settings.attrs.ul);

        	if(level > 1) $ul.attr({'role': 'group'});

	        $.each(items, function(i, item) {
	        	$ul.append(getTreeItem(item, level, $ul));
	        });

	        settings.onUl($ul, items, settings);

	        return $ul;
        };

        $this.attr(settings.attrs.root).html(getTree(settings.data, 1));

        settings.onFinish($this, settings.data, settings);

        return $this;
    };
 
}(jQuery));