import React from 'react';
import PropTypes from 'prop-types';

import {ButtonGroup, Button, Tooltip} from '@salesforce/design-system-react';
 
const propTypes = {
    totalItems: PropTypes.number.isRequired,
    onChangePage: PropTypes.func.isRequired,
    currentPage: PropTypes.number,
    pageSize: PropTypes.number,
}
 
const defaultProps = {
    currentPage: 1,
    pageSize: 10
}
 
class SLDSPaginator extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            pager: {}
        };
    }
 
    componentWillMount() {
        // set page if items array isn't empty
        if (this.props.totalItems) {
            this.setPage(this.props.currentPage, true);
        }
    }
 
    setPage(page, isInitialLoad) {
        var { totalItems, pageSize } = this.props;
        var pager = this.state.pager;
 
        if (page < 1 || page > pager.totalPages) {
            return;
        }
 
        // get new pager object for specified page
        pager = this.getPager(totalItems, page, pageSize);
 
        // update state
        this.setState({ pager: pager });
 
        // call change page function in parent component
        this.props.onChangePage(page, false || isInitialLoad);
    }
 
    getPager(totalItems, currentPage, pageSize) {
        // default to first page
        currentPage = currentPage || 1;
 
        // default page size is 10
        pageSize = pageSize || 10;

        var isNarrow = false;

        if(totalItems > 10000) {
            totalItems = 10000;
            isNarrow = true;
        }
 
        // calculate total pages
        var totalPages = Math.ceil(totalItems / pageSize);
 
        var startPage, endPage;
        if (totalPages <= 10) {
            // less than 10 total pages so show all
            startPage = 1;
            endPage = totalPages;
        } else {
            // more than 10 total pages so calculate start and end pages
            if (currentPage <= 6) {
                startPage = 1;
                endPage = 10;
            } else if (isNarrow && (currentPage + 3) >= totalPages) {
                startPage = totalPages - 8;
                endPage = totalPages;
            } else if ((currentPage + 4) >= totalPages) {
                startPage = totalPages - 9;
                endPage = totalPages;
            } else {
                startPage = currentPage - 5;
                endPage = currentPage + 4;
            }
        }
 
        // calculate start and end item indexes
        var startIndex = (currentPage - 1) * pageSize;
        var endIndex = Math.min(startIndex + pageSize - 1, totalItems - 1);
 
        // create an array of pages to ng-repeat in the pager control
        var pages = [...Array((endPage + 1) - startPage).keys()].map(i => startPage + i);
 
        // return object with all pager properties required by the view
        return {
            totalItems: totalItems,
            currentPage: currentPage,
            pageSize: pageSize,
            totalPages: totalPages,
            startPage: startPage,
            endPage: endPage,
            startIndex: startIndex,
            endIndex: endIndex,
            pages: pages,
            isNarrow: isNarrow
        };
    }
 
    render() {
        var pager = this.state.pager;
 
        if (!pager.pages || pager.pages.length <= 1) {
            // don't display pager if there is only 1 page
            return null;
        }
 
        return (
            <ButtonGroup>
                <Button iconCategory="utility"
						iconName="left"
						iconPosition="left" label="First" variant="neutral" onClick={() => this.setPage(1)} disabled={pager.currentPage === 1} />
                <Button iconCategory="utility"
						iconName="chevronleft"
						iconPosition="left" variant="neutral" onClick={() => this.setPage(pager.currentPage - 1)} disabled={pager.currentPage === 1} />
                {pager.pages.map((page, index) => {
                    return (
                        <Button key={'SLDSPaginator' + Date.now() + index} label={page} variant={pager.currentPage === page ? 'brand' : 'neutral'} onClick={() => this.setPage(page)} />
                    )
                })}
                {pager.isNarrow && pager.currentPage >= (pager.totalPages - 3) && (
                    <Tooltip
                        align="top right"
                        content="The list is too long, try adding more filters to optimize the results."
                    >
                        <Button label="&middot;&middot;&middot;" variant="neutral" />
                    </Tooltip>
                )}
                <Button iconCategory="utility"
						iconName="chevronright"
						iconPosition="right" variant="neutral" onClick={() => this.setPage(pager.currentPage + 1)} disabled={pager.currentPage === pager.totalPages} />
                <Button iconCategory="utility"
						iconName="right"
						iconPosition="right" label="Last" variant="neutral" onClick={() => this.setPage(pager.totalPages)} disabled={pager.currentPage === pager.totalPages} />
            </ButtonGroup>
        );
    }
}
 
SLDSPaginator.propTypes = propTypes;
SLDSPaginator.defaultProps = defaultProps;
export default SLDSPaginator;