/* eslint-disable no-multi-str */
import React from "react";
import "../public/js/jquery.sldsTree.js";

import { Spinner } from "@salesforce/design-system-react";

import { $, baseUrl, GetParams } from "../lib/grax-data-lake";

import { getCookie, setCookie } from "../lib/utils";

import _ from "lodash";

var selectedDataIndex;
var cookieSubfix = "Fields";

var renderFieldListSelector = function(
  $fieldListSelector,
  $fieldlist,
  data,
  onLoadHandler,
  onChangeHandler
) {
  data.sort(function(a, b) {
    if (a.label > b.label) {
      return 1;
    }

    if (a.label < b.label) {
      return -1;
    }

    return 0;
  });

  var domPrefix = Date.now() + selectedDataIndex + cookieSubfix;

  $fieldListSelector.sldsTree({
    data: data,
    attrs: {
      root: { class: "slds-scrollable_y", style: "max-height: 25vw" }
    },
    onDiv: function($div, item, settings) {
      $div.append(
        '<div className="slds-size_1-of-1">\
            <div className="slds-form-element">\
                <div className="slds-form-element__control">\
                    <span className="slds-checkbox">\
                    <input name="field_' +
          domPrefix +
          item[settings.items.id] +
          '" id="field_' +
          domPrefix +
          item[settings.items.id] +
          '" type="checkbox" ' +
          (item.isSelected ? "checked" : "") +
          ' />\
                    <label className="slds-checkbox__label" for="field_' +
          domPrefix +
          item[settings.items.id] +
          '">\
                        <span className="slds-checkbox_faux"></span>\
                        <span className="slds-form-element__label">' +
          item[settings.items.label] +
          "</span>\
                    </label>\
                    </span>\
                </div>\
                </div>\
            </div>"
      );
    },
    onFinish: function($root, items, settings) {
      var fields = {};
      var countChecked = 1;

      var getSelectedFields = function() {
        var selectedFields = [];

        $.each(fields, function(field, selected) {
          if (selected) {
            selectedFields.push(field);
          }
        });

        return selectedFields;
      };

      var cookieSelected = getCookie(selectedDataIndex + cookieSubfix);

      var updateValue = function(eventHandler) {
        var selectedFieldList = getSelectedFields();
        var selectedFields = selectedFieldList.join(",");

        $fieldlist.val(selectedFields);
        setCookie(selectedDataIndex + cookieSubfix, selectedFields);

        if (eventHandler) {
          eventHandler(selectedFieldList);
        }
      };

      $.each(items, function(i, item) {
        var id = item[settings.items.id];

        if (cookieSelected) {
          fields[id] = cookieSelected.split(",").indexOf(id) !== -1;
        } else {
          fields[id] = item.isSelected;
        }

        $("#field_" + domPrefix + id)
          .on("change", function(e) {
            if (e.target.checked && countChecked >= 9) {
              e.target.checked = false;
              e.preventDefault();

              return false;
            }

            fields[id] = e.target.checked;

            if (e.target.checked) {
              countChecked++;
            } else {
              countChecked--;
            }

            updateValue(onChangeHandler);
          })
          .prop("checked", fields[id]);

        //updateValue();
      });

      updateValue(onLoadHandler);
    }
  });
};

export default class GRAXTree extends React.Component {
  constructor(props) {
    super(props);

    this._isMounted = false;

    this.state = {
      items: [],
      isLoaded: false
    };
  }

  getMappedFieldSelection(selectedFieldList) {
    return _.reduce(
      this.state.items,
      (_selection, fieldItem) => {
        if (selectedFieldList.indexOf(fieldItem.id) !== -1) {
          _selection.push(fieldItem);
        }

        return _selection;
      },
      []
    );
  }

  onLoadHandler(selectedFieldList) {
    if (this.props.onLoad) {
      this.props.onLoad(
        this.getMappedFieldSelection(selectedFieldList),
        selectedFieldList,
        this.state.items
      );
    }
  }

  onChangeHandler(selectedFieldList) {
    if (this.props.onChange) {
      this.props.onChange(
        this.getMappedFieldSelection(selectedFieldList),
        selectedFieldList,
        this.state.items
      );
    }
  }

  componentDidMount() {
    this._isMounted = true;

    selectedDataIndex = this.props.dataIndex;

    if (this.props.cookieSubfix) {
      cookieSubfix += this.props.cookieSubfix;
    }

    if (this.props.selection) {
      setCookie(selectedDataIndex + cookieSubfix, this.props.selection.join(","));
    }

    $.ajax({
      type: "GET",
      //dataType: 'jsonp',
      url:
        baseUrl +
        "/grax-app/grax-data-lake/getDataTypeFields/" +
        selectedDataIndex +
        GetParams({ action: "load" }),
      data: {},
      success: function(data) {
        if (this._isMounted) {
          this.setState({
            items: _.map(data, (val, key) => ({
              id: key,
              label: key,
              type: val,
              isSelected: !getCookie(selectedDataIndex + cookieSubfix) && key === "Id"
            })),
            isLoaded: true
          });
        }

        this.$el = $(this.el);
        this.$fieldlist = $(this.fieldList);

        renderFieldListSelector(
          this.$el,
          this.$fieldlist,
          this.state.items,
          this.onLoadHandler.bind(this),
          this.onChangeHandler.bind(this)
        );
      }.bind(this),
      error: function(xhr, status, err) {
        console.error(xhr, status, err);

        if (this._isMounted) {
          this.setState({
            isLoaded: true,
            error: err.toString()
          });
        }
      }.bind(this)
    });
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  shouldComponentUpdate(nextProps, nextState) {
    return this.state.isLoaded !== nextState.isLoaded;
  }

  render() {
    return (
      <div style={{ position: "relative", minHeight: "2rem" }}>
        {!this.state.isLoaded ? (
          <Spinner size="small" variant="base" />
        ) : (
          <React.Fragment>
            <input type="hidden" ref={fieldlist => (this.fieldlist = fieldlist)} />
            <div ref={el => (this.el = el)} />
          </React.Fragment>
        )}
      </div>
    );
  }
}
