import $ from "jquery";

// export const isDev = window.location.port !== '80';

export const isDev = false;

export const baseUrl = isDev ? process.env.REACT_APP_BASE_URL : "";

if (process && process.env && !window.__grax__) {
  window.__grax__ = {
    orgid: process.env.REACT_APP_ORGID,
    pageParams: { authorization: process.env.REACT_APP_BASIC_TOKEN }
  };
}

export const token = window.__grax__.token;
export const _csrf = window.__grax__._csrf;
export const orgid = window.__grax__.orgid;
export const userid = window.__grax__.userid;
export const sessionid = null;
export const endpoint = null;
export const pageParams = window.__grax__.pageParams || {};

export function GetParams(params) {
  if (params === null || params === undefined) params = {};

  if (params.action === null || params.action === undefined || params.action === "")
    params.action = "load";

  return "?" + $.param(params);
}

function csrfSafeMethod(method) {
  // these HTTP methods do not require CSRF protection
  return /^(GET|HEAD|OPTIONS|TRACE)$/.test(method);
}
$.ajaxSetup({
  beforeSend: function(xhr, settings) {
    xhr.setRequestHeader("sfdcorgid", orgid);

    if (pageParams.authorization != null) {
      xhr.setRequestHeader("Authorization", "Basic " + pageParams.authorization);
    } else {
      xhr.setRequestHeader("Authorization", "Bearer " + token);
    }

    if (!csrfSafeMethod(settings.type) && !this.crossDomain) {
      xhr.setRequestHeader("X-CSRFToken", _csrf);
    }

    xhr.withCredentials = true;

    if (settings.skipFail !== true) {
      xhr.fail((jqXHR, textStatus, errorThrown) => {
        console.error(jqXHR);

        if (jqXHR.status !== 404 && errorThrown !== "Not Found") {
          window.showError(jqXHR.statusText + ": " + jqXHR.responseText);
        }
      });
    }
  }
});

export { $ };
