/* eslint-disable no-useless-constructor */
import React from "react";
import ReactDOM from "react-dom";

import _ from "lodash";

import { baseUrl, GetParams, token, orgid } from "../lib/grax-data-lake";

const portalsRoot = document.getElementById("portals");

class GRAXCSVExportForm extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return ReactDOM.createPortal(
      <form
        id={this.props.formId}
        action={
          baseUrl +
          "/grax-app/grax-data-lake/export/" +
          this.props.params.source +
          "/csv" +
          GetParams({
            action: "export",
            token,
            orgid
          })
        }
        target="_blank"
        method="POST"
        encType="application/x-www-form-urlencoded"
        style={{
          margin: 0,
          padding: 0,
          border: 0,
          display: "inline-block"
        }}
      >
        {_.map(this.props.params, function(val, key) {
          return (
            <input type="hidden" key={String(Date.now()) + key} name={key} value={val || ""} />
          );
        })}
      </form>,
      portalsRoot
    );
  }
}

export default class GRAXCSVExportButton extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      formId: "csvExport" + Date.now()
    };
  }

  render() {
    return (
      <React.Fragment>
        <GRAXCSVExportForm formId={this.state.formId} params={this.props.params} />
        <button className="slds-button slds-button_neutral" type="submit" form={this.state.formId}>
          Export to CSV
        </button>
      </React.Fragment>
    );
  }
}
